'use strict';

const e = React.createElement;


const MenuItem = (props) => {
  const { name, label, url, active } = props;

      if(active === name) return <a href={url} className="link-active"><li>{label}</li></a>
    
      return  <a href={url} ><li>{label}</li></a>
  };

class MainNavigation extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      active: false,
      pages: [
        {
          id: 0,
          name: "education-visitor",
          label: "Education",
          url: "./education-visitor.html",
          show: true,
        },
        {
          id: 1,
          name: "trading-visitor",
          label: "Trading",
          url: "./trading-visitor.html",
          show: true,
        },
        {
          id: 2,
          name: "free-content",
          label: "Free content",
          url: "./free-content.html",
          show: true,
        },
        {
          id: 3,
          name: "about-us",
          label: "About us",
          url: "./about-us.html",
          show: true,
        },
        {
          id: 4,
          name: "pricing",
          label: "Pricing",
          url: "./pricing.html",
          show: true,
        },
        {
          id: 5,
          name: "support",
          label: "Support",
          url: "./support.html",
          show: true,
        },
      ]
    };
  };

  toggleClass = () => {
    this.setState({ active: !this.state.active });
  };

  render() { 
    return (
      <div className="main-navigation-bar">
      <div className="main-navigation-container">
        <div className="main-navigation-online">
          <div className="main-navigation-online__container">
            <div className="ac-membersonline">
              <div className="ac-membersonline__indicator"> </div><span>{this.props.members} Members online</span>
            </div>
          </div>
        </div>
        <div className="main-navigation-menu">
          <div className="main-navigation-menu__container">
          <div className="main-navigation-menu__logo">
            <a href="/">
            <span className="font-bold color-lightgrey">Altcoin Traders</span>
            </a>
          </div>
          <div className="main-navigation-menu__items">
            <nav className="menu-horizontal hide-on-tablet">
              <ul>
                {this.state.pages.map((item) => <MenuItem key={item.id} name={item.name} label={item.label} url={item.url} active={this.props.active} />)}
              </ul>
            </nav>
          </div>
          <div className="hide-on-desktop">
            <div className="button button--color-dark mobile-shrink" onClick={this.toggleClass} >
              Menu
            </div>
          </div>
          <div className="hide-on-tablet">
            <a href="./login.html">
            <div className="button button__small button--color-dark">
              Sign in
            </div>
            </a>
          </div>
          </div>
        </div>
        <div className={this.state.active ? "main-navigation-mobile" : "main-navigation-mobile--close"}>
         <div className="main-navigation-mobile__container">
            <div className="main-navigation-mobile__close">
              <div className="button button--color-primary button__small mobile-shrink" onClick={this.toggleClass} >
                  <figure className="color-invert margin-right-8">
                    <img src={require('/assets/icons/x.svg')}/>
                  </figure>
                  Close
              </div>
            </div>
            <nav className="menu-verticle">
              <ul>
                <a href="/"><li>Home</li></a>
                {this.state.pages.map((item) => <MenuItem key={item.id} name={item.name} label={item.label} url={item.url} active={this.props.active} />)}
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </div>
    );
  }
}

document.querySelectorAll('#mainNavigation').forEach(domContainer => { 
  const activeProp = domContainer.dataset.active;
  const membersProp = domContainer.dataset.members;
  const modeProp = domContainer.dataset.mode;

  ReactDOM.render(
    e(MainNavigation, {active: activeProp, members: membersProp, mode: modeProp}), 
    domContainer);	
});